async function convertSvgToPng(dataUrl) {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.src = dataUrl;
    img.onload = () => {
      const canvas = document.createElement("canvas");
      canvas.width = img.width;
      canvas.height = img.height;
      const ctx = canvas.getContext("2d");
      ctx.drawImage(img, 0, 0);
      resolve(canvas.toDataURL("image/png"));
    };
    img.onerror = reject;
  });
}

// versionv3
// Helper function to render markdown as an image with larger text
const renderMarkdownToImage = async (markdownText) => {
  // Convert markdown to HTML
  const htmlContent = marked(markdownText);

  // Create a hidden div to render the HTML content
  const container = document.createElement("div");
  container.innerHTML = htmlContent;

  // Apply some styling to the container to increase font size
  container.style.position = "absolute";
  container.style.left = "-9999px"; // Hide it offscreen
  container.style.fontSize = "18px"; // Increase text size
  container.style.lineHeight = "1.6"; // Adjust line height for readability
  container.style.fontFamily = "Arial, sans-serif"; // Use a common font
  container.style.width = "100%"; // Allow it to stretch to fit the content

  // Append to the body temporarily to render
  document.body.appendChild(container);

  // Render the container as an image
  const canvas = await html2canvas(container);
  document.body.removeChild(container); // Clean up after rendering
  return canvas.toDataURL("image/png");
};

// Function to read file as data URL
async function readFileAsDataUrl(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = () => resolve(reader.result);
    reader.onerror = reject;
    reader.readAsDataURL(file);
  });
}

const downloadPDF = async () => {
  try {
    const pdf = new jsPDF();
    const pageHeight = pdf.internal.pageSize.height;
    const pageWidth = pdf.internal.pageSize.width;
    const margin = 10; // Margin around the content
    let yOffset = margin;

    // 1. Add images to the PDF
    for (let i = 0; i < filesData.length; i++) {
      const file = filesData[i];
      const fileType = file.type;

      if (fileType.startsWith("image/")) {
        let dataUrl;
        
        if (fileType === "image/svg+xml") {
          dataUrl = await convertSvgToPng(await readFileAsDataUrl(file));
        } else {
          dataUrl = await readFileAsDataUrl(file);
        }

        const img = new Image();
        await new Promise((resolve, reject) => {
          img.onload = resolve;
          img.onerror = reject;
          img.src = dataUrl;
        });

        // Calculate dimensions without enlarging images
        const imgAspectRatio = img.width / img.height;
        const maxImgWidth = pageWidth - 2 * margin;
        const imgWidth = Math.min(img.width, maxImgWidth);
        const imgHeight = imgWidth / imgAspectRatio;

        if (yOffset + imgHeight > pageHeight - margin) {
          pdf.addPage();
          yOffset = margin;
        }

        pdf.addImage(dataUrl, "JPEG", margin, yOffset, imgWidth, imgHeight);
        yOffset += imgHeight + margin;
      } else if (fileType === "application/pdf") {
        const existingPdfBytes = await file.arrayBuffer();
        const existingPdf = await PDFDocument.load(existingPdfBytes);
        const copiedPages = await pdf.copyPages(existingPdf, existingPdf.getPageIndices());

        copiedPages.forEach((page) => {
          pdf.addPage(page);
        });
      }
    }

    // 2. Render markdown content as image and add it to PDF
    if (streamData) {
      const markdownImage = await renderMarkdownToImage(streamData);

      // Add a new page if necessary for the markdown content
      if (yOffset + pageHeight > pageHeight - margin) {
        pdf.addPage();
        yOffset = margin;
      }

      // Add markdown image to the PDF
      pdf.addImage(markdownImage, "PNG", margin, yOffset, pageWidth - 2 * margin, 0);
    }

    // Save the PDF
    pdf.save(`Visual_assessment_report_with_${selectedModel?.name}.pdf`);
  } catch (error) {
    console.error("Error generating PDF:", error);
    alert("Error generating PDF. Please check your files and try again.");
  }
};

