
import React, { useEffect, useState } from 'react';
import UploadFileIcon from '@mui/icons-material/UploadFile';
import ImageIcon from '@mui/icons-material/Image';
import CloseIcon from '@mui/icons-material/Close';
import ZoomInIcon from '@mui/icons-material/ZoomIn';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import ExpandIcon from './../assets/images/expand.png'
import RestartAltIcon from '@mui/icons-material/RestartAlt';
import Generate from './../assets/images/generate.png'
import { 
  Card, 
  CardContent, 
  Typography, 
  Box, 
  Button, 
  Switch,
  IconButton,
  Grid,
  Stack,
  Paper,
  Alert,
  TextField,
  Dialog,
  DialogContent
} from '@mui/material';


const MultiFileUpload = ({ handleData, load, handleClear, singlePage,buttonDisable,handleFullScreen, holdButton}) => {
  const [files, setFiles] = useState([]);
  const [showThumbnail, setShowThumbnail] = useState(false);
  const [dragActive, setDragActive] = useState(false);
  const [error, setError] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [enlargedImage, setEnlargedImage] = useState(null);
console.log(singlePage,"singlePage")
  useEffect(() => {
    handleData(files);
  }, [files, handleData]);

  const isValidImageUrl = (url) => url.match(/\.(jpeg|jpg|png)$/i);

  const createFileFromUrl = async (url) => {
    try {
      const response = await fetch(url);
      const blob = await response.blob();
      const fileName = url.substring(url.lastIndexOf('/') + 1);
      return new File([blob], fileName, { type: blob.type });
    } catch (error) {
      throw new Error('Invalid image URL');
    }
  };

  const handleUrlPaste = async (e) => {
    const url = e.target.value.trim();
    setImageUrl(url);

    // if (!url) return;

    // if (!isValidImageUrl(url)) {
    //   setError('Invalid image URL. Only JPG, JPEG, and PNG formats are supported.');
    //   setTimeout(() => setError(''), 3000);
    //   return;
    // }

    try {
      const file = await createFileFromUrl(url);
      if (isDuplicateFile(file)) {
        setError(`Duplicate file: ${file.name}`);
        setTimeout(() => setError(''), 3000);
      } else {
        setFiles((prevFiles) => [...prevFiles, file]);
        setImageUrl('');
      }
    } catch (error) {
      setError('Unable to load image from URL. Please check the URL and try again.');
      setTimeout(() => setError(''), 3000);
    }
  };

  const isDuplicateFile = (newFile) =>
    files.some((existingFile) => existingFile.name === newFile.name && existingFile.size === newFile.size);

  const processFiles = (newFiles) => {
    const duplicates = [];
    const validFiles = [];

    Array.from(newFiles).forEach((file) => {
      if (isDuplicateFile(file)) {
        duplicates.push(file.name);
      } else {
        validFiles.push(file);
      }
    });

    if (duplicates.length > 0) {
      setError(`Duplicate file(s) detected: ${duplicates.join(', ')}`);
      setTimeout(() => setError(''), 3000);
    }

    if (validFiles.length > 0) {
      setFiles((prevFiles) => [...prevFiles, ...validFiles]);
    }
  };

  const handleDrag = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === 'dragenter' || e.type === 'dragover') {
      setDragActive(true);
    } else if (e.type === 'dragleave') {
      setDragActive(false);
    }
  };

  const handleDrop = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    processFiles(e.dataTransfer.files);
  };

  const handleFileSelect = (e) => {
    processFiles(e.target.files);
    e.target.value = null; // clear the input value to allow re-selection of the same file
  };

  const removeFile = (indexToRemove) => {
    setFiles((prevFiles) => {
      const updatedFiles = prevFiles.filter((_, index) => index !== indexToRemove);
      if (updatedFiles.length === 0) {
        handleClear();
      }
      return [...updatedFiles];
    });
    setTimeout(() => setFiles((files) => [...files]), 0);
  };

  const removeAll = () => {
    handleClear([]);
    setFiles([]);
  };

  const handleEnlarge = (file) => {
    setEnlargedImage(URL.createObjectURL(file));
  };

  const handleCloseEnlarged = () => {
    setEnlargedImage(null);
  };

  return (
    <Box sx={{ height: '100%', display: 'flex', flexDirection: 'column', overflow: 'hidden' }}>
  
    <Paper
  variant="outlined"
  sx={{
    // p: !singlePage ? 4 : 0,
    p: {
      xs: 1, // Small devices
      md: !singlePage ? 2 : 1, // Medium devices
      lg:  !singlePage ? 4 : 1, // Large devices
      xl: !singlePage? 5:2, // Extra-large devices
    },
    backgroundColor: '#F6F7F8',
    border: `1.5px dashed ${dragActive ? 'primary.main' : 'grey.300'}`,
    borderRadius: '20px',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    gap: 0.5,
    cursor: 'pointer',
    // mb: 2,
  }}
  onDragEnter={handleDrag}
  onDragLeave={handleDrag}
  onDragOver={handleDrag}
  onDrop={handleDrop}
>
  <Box sx={{ p: 0, bgcolor: 'primary.50', borderRadius: '50%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
    <CloudUploadIcon sx={{ fontSize: 32, color: 'primary.main' }} />
  </Box>
  <Box sx={{ textAlign: 'center', mb: !singlePage ? 1 : 1 }}>
    <Typography
      variant={singlePage ? 'body1' : 'h6'}
      sx={{
        fontSize: {
          xs: '1rem', // Small devices
          md: '1.125rem', // Medium devices
          lg: load?"1rem":'1.125rem', // Large devices
          xl: '1.75rem', // Extra-large devices and above
        },
      }}
    >
      Drop or select file{' '}
      <label style={{ cursor: 'pointer', color: 'blue', textDecoration: 'underline' }}>
        Browse
        <input type="file" hidden multiple onChange={handleFileSelect} />
      </label>
    </Typography>
  {!load && <Typography
    variant={singlePage ? 'body2' : 'body'}
    color="text.secondary"
    sx={{
      fontSize: {
        xs: '0.6rem', // Small devices
        md: '0.5rem', // Medium devices
        lg: '1rem', // Large devices
        xl: '1.125rem', // Extra-large devices and above
      },
    }}
  >
    Limit 200 MB per file. Supported Formats: JPG, JPEG, PNG.
  </Typography>}
  </Box>
</Paper>


    <Typography variant="h6" sx={{ padding: '0px', margin: 1,  alignSelf: 'center', textAlign: "center", 
    // fontSize: {
    //       xs: '0.6rem', // Small devices
    //       md: '1.125rem', // Medium devices
    //       lg: '1rem', // Large devices
    //       xl: '5 rem', // Extra-large devices and above
    //     },
   }}>
      or
    </Typography>

    <Paper
      variant="outlined"
      sx={{
        backgroundColor: '#F6F7F8',
        border: `1.5px dashed ${dragActive ? 'primary.main' : 'grey.300'}`,
        borderRadius: '10px',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        // p: !singlePage ? 1 : 0,
        p: {
          xs: 1, // Small devices
          md: 1, // Medium devices
          lg:  !singlePage ? 1: 0, // Large devices
          xl: !singlePage? 2:1, // Extra-large devices
        },
        gap: 0.5,
        cursor: 'pointer',
        // mb: 2,
      }}
    >
      <Box sx={{ textAlign: 'center', width: '100%' }}>
        {/* <TextField
          size="small"
          placeholder="Paste image URL here"
          fullWidth
          variant="standard"
          value={imageUrl}
          onChange={(e) => setImageUrl(e.target.value)}
          onBlur={handleUrlPaste}
          onKeyPress={(e) => {
            if (e.key === 'Enter') {
              handleUrlPaste(e);
            }
          }}
          InputProps={{
            disableUnderline: true,
          }}
          sx={{
            paddingLeft: '10px',
            '& .MuiInputBase-root': {
              borderBottom: 'none',
            },
          }}
        /> */}
         <TextField
      size="small"
      placeholder="Paste image URL here"
      fullWidth
      variant="standard"
      value={imageUrl}
      onChange={(e) => setImageUrl(e.target.value)}
      onBlur={handleUrlPaste}
      onKeyPress={(e) => {
        if (e.key === 'Enter') {
          handleUrlPaste(e);
        }
      }}
      InputProps={{
        disableUnderline: true,
        sx: {
          whiteSpace: 'nowrap',
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          '& input': {
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            textOverflow: 'ellipsis'
          }
        }
      }}
      sx={{
        paddingLeft: '10px',
        '& .MuiInputBase-root': {
          borderBottom: 'none',
        },
        '& .MuiInputBase-input': {
          whiteSpace: 'nowrap',
          overflow: 'hidden',
          textOverflow: 'ellipsis'
        }
      }}
    />
      </Box>
    </Paper>

    
      {files?.length > 0 && (
  <>
    <Box sx={{ display: 'flex', margin: 1, p: 0, alignItems: 'center', justifyContent: 'flex-end' }}>
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.2 }}>
        <Typography variant="body2">Preview Thumbnail</Typography>
        <Switch size="small" checked={showThumbnail} onChange={(e) => setShowThumbnail(e.target.checked)} />
      </Box>
    </Box>
    <Box sx={{ flexGrow: 1, overflow: 'hidden', 
    // backgroundColor: "yellow"
     }}>
      <Box sx={{ height: '100%', overflowY: 'auto', p: 1 }}>
        {showThumbnail ? (
          <Stack spacing={1}>
            {/* Thumbnail view code remains the same */}
            {files?.map((file, index) => (
  <Box
    key={index}
    sx={{
      display: 'flex',
      alignItems: 'center',
      p: 1,
      borderRadius: 1,
      bgcolor: '#f5f5f5',
      position: 'relative'
    }}
  >
    {file.type.startsWith('image/') ? (
      <Box
        component="img"
        src={URL.createObjectURL(file)}
        alt={file.name}
        sx={{
          width: 48,
          height: 48,
          borderRadius: 1,
          objectFit: 'cover',
          mr: 2
        }}
      />
    ) : (
      <Box
        sx={{
          width: 48,
          height: 48,
          borderRadius: 1,
          bgcolor: '#00A76F',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          color: 'white',
          mr: 2
        }}
      >
        <ImageIcon />
      </Box>
    )}
    <Box sx={{ flex: 1, minWidth: 0 }}>
      <Typography 
        variant="body2" 
        sx={{ 
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap'
        }}
      >
        {file.name}
      </Typography>
      <Typography 
        variant="caption" 
        color="text.secondary"
      >
        {(file.size / (1024 * 1024)).toFixed(2)} MB
      </Typography>
    </Box>
    <Box sx={{ display: 'flex', gap: 1 }}>
      <IconButton
        size="small"
        onClick={() => handleEnlarge(file)}
        sx={{
          color: 'text.secondary',
          '&:hover': {
            color: 'primary.main'
          }
        }}
      >
        <img src={ExpandIcon} alt="Expand Logo" style={{ width: '16px' }} />
      </IconButton>
      <IconButton
        size="small"
        onClick={() => removeFile(index)}
        sx={{
          color: 'text.secondary',
          '&:hover': {
            color: 'error.main'
          }
        }}
      >
        <CloseIcon sx={{ fontSize: 16 }} />
      </IconButton>
    </Box>
  </Box>
))}
          </Stack>
        ) : (
          <Grid container spacing={2}>
            {files?.map((file, index) => (
              <Grid item xs={12} sm={6} md={6} lg={load?6:4}  key={index} sx={{ marginBottom: 2 }}
              
              >
                <Box
                  sx={{
                    position: 'relative',
                    width: '100%',
                    paddingTop: '100%', // This creates a 1:1 aspect ratio
                    borderRadius: 1,
                    overflow: 'hidden',
                  }}
                >
                  {file.type.startsWith('image/') ? (
                    <Box
                      component="img"
                      src={URL.createObjectURL(file)}
                      alt={file.name}
                      sx={{
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        width: '100%',
                        height: '100%',
                        objectFit: 'cover',
                      }}
                    />
                  ) : (
                    <Box
                      sx={{
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        width: '100%',
                        height: '100%',
                        bgcolor: '#00A76F',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        color: 'white',
                      }}
                    >
                      <ImageIcon sx={{ fontSize: '3rem' }} />
                    </Box>
                  )}
                  <IconButton
                    size="small"
                    onClick={() => removeFile(index)}
                    sx={{
                      position: 'absolute',
                      top: 8,
                      right: 8,
                      color: '#fff',
                      bgcolor: 'rgba(0, 0, 0, 0.5)',
                      '&:hover': {
                        bgcolor: 'rgba(0, 0, 0, 0.7)',
                      },
                      padding: '4px',
                    }}
                  >
                    <CloseIcon sx={{ fontSize: 16 }} />
                  </IconButton>
                  <IconButton
                    size="small"
                    onClick={() => handleEnlarge(file)}
                    sx={{
                      position: 'absolute',
                      bottom: 8,
                      right: 8,
                      color: '#fff',
                      bgcolor: 'rgba(0, 0, 0, 0.5)',
                      '&:hover': {
                        bgcolor: 'rgba(0, 0, 0, 0.7)',
                      },
                      padding: '4px',
                    }}
                  >
                    <img src={ExpandIcon} alt="Expand Logo" style={{ width: '20px' }} />
                  </IconButton>
                </Box>
              </Grid>
            ))}
          </Grid>
        )}
      </Box>
    </Box>
    
    {/* <Box sx={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'flex-end', mt: 2 }}>
      <Button
        variant="outlined"
        color="error"
        size="small"
        sx={{
          color: '#637381',
          borderColor: '#637381',
          '&:hover': {
            borderColor: '#212B36',
            color: '#212B36',
          },
        }}
        onClick={removeAll}
      >
        Clear All
      </Button>
    </Box> */}
     <Box 
            sx={{
              flexShrink: 0,
              padding: "10px",
              backgroundColor: 'inherit',
              textAlign: 'center',
              paddingBottom:2
            }}
          >
           <Button 
              onClick={handleFullScreen} 
              variant="contained" 
              color="primary" 
         
              disabled={buttonDisable || holdButton}
              startIcon={
                <Box
                  component="img"
                  src={Generate}
                  alt="Company Logo"
                  sx={{
                    width: "24px",
                    height: "24px",
                  }}
                />
              }
              sx={{
                borderRadius: "50px",
                transition: "opacity 0.3s ease, transform 0.3s ease",
                opacity: buttonDisable || holdButton ? 0.5 : 1,
                transform: buttonDisable || holdButton ? "scale(0.95)" : "scale(1)",
                // fontSize: {
                //   md: '0.6rem',     // default for small screens
                //   lg: '1rem',  // larger font for large screens
                //   xl: '1.5rem'    // extra-large font for extra-large screens
                // }
            
            
              }}
            >
              Generate
            </Button>
          </Box>
  </>
)}

      {error && (
        <Alert severity="error" onClose={() => setError('')}>
          {error}
        </Alert>
      )}

      {!!enlargedImage && (
        <Dialog
          open={true}
          onClose={handleCloseEnlarged}
          maxWidth={false}
          fullWidth
          PaperProps={{
            style: {
              backgroundColor: 'transparent',
              boxShadow: 'none',
              margin: 0,
              width: '30%',
              maxWidth: '30%',
              
            },
          }}
          BackdropProps={{
            style: {
              backgroundColor: 'rgba(0, 0, 0, 0.8)',
            },
          }}
        >
          <DialogContent
            className="p-0 flex justify-center items-center"
            style={{
              padding: 0,
              height: '100vh',
              width: '100%',
              maxWidth: '100%',
              maxHeight: '40vh',
              margin: 0,
            }}
          >
            <img
              src={enlargedImage}
              alt="Enlarged view"
              className="w-full h-full object-contain"
              style={{
                width: '90%',
                height: '90%',
              }}
            />
          </DialogContent>
        </Dialog>
      )}
    </Box>
  );
};

export default MultiFileUpload;


